@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600&display=swap);

.videoCallBack{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-color: #4f4f4f;
}

.innerVideoCallBack{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.middlepopup{
    width:250px;
    height:200px;
    position: relative;
    top: 45%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.popupImageIcon{
    width: 124px;
    height: 126px;
}

.ringingText{
    text-align: center;
    font: 100;
    font-family: 'Lato';
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-top: 5px;
}

.videoOptions{
}

.videoOptions > img{
    position: absolute;
    bottom: 25px;
    width: 55px;
    height: 55px;
}

.videoOptions>img:hover{
    position: absolute;
    bottom: 25px;
    cursor: pointer;
}

.viewVideo{
    position: absolute;
    left: 0px;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
}

.ownVideo{
    position: absolute;
    width: 209px;
    height: 118px;
    left: 30px;
    bottom:100px;
    z-index: 2;
}

.popupText{
    font-family: Lato;
    font-style: normal;
    font-size: 17px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.02em;
    margin-bottom: 5px;
    color: #FFFFFF;
}

#exit:hover{
    opacity: 0.6;
}

#messageLogo:hover{
    opacity: 0.6;
}

#messageUnreadLogo:hover{
    opacity: 0.6;
}

.callendpopup{
    position: absolute;
    width:250px;
    height:200px;
    top: 60px;
    left: 50%;
    margin-left: -125px;
}

.callEndComplete{
    position: relative;
    width: 100%;
    left:0px;
    top:0px;
    height: 100%;
    overflow-y: scroll;
}

.callEndedImageIcon{
    width: 60px;
    height: 60px;
}

.time{
    text-align: center;
    font-family: Lato;
    font-style: normal;
    font-size: 96px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.02em;
    color: #FFFFFF;
}

.chatbox{
    position: absolute;
    left: 155px;
    bottom: 25px;
    width: 15%;
    height: 50%;
    background-color: #FFFFFF;
    z-index: 4;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    overflow: hidden;
}

.sendMessage{
    position: absolute;
    width: 100%;
    height: 60px;
    bottom: 0px;

    /* White/100% */
    background: #FFFFFF;
    box-shadow: 0px -0.5px 0px rgba(0, 0, 0, 0.1);
    border-radius: 0px 0px 0px 12px;
}

.sendMessageIcon{
    position: absolute;
    right: 5%;
    bottom: 20px;
    width: 20px;
    margin: auto;
}

.sendMessageIcon:hover{
    position: absolute;
    right: 5%;
    bottom: 20px;
    width: 20px;
    margin: auto;
    cursor: pointer;
}

.sendMessageBox{
    position: absolute;
    left: 5%;
    bottom: 0px;
    width: 80%;
    border: none;
    font-family: Lato;
    font-style: normal;
    font-size: 15px;
    padding-bottom: 22px;
    padding-top: 20px;
}

.sendMessageBox:focus{
    position: absolute;
    left: 5%;
    bottom: 0px;
    width: 75%;
    border: none;
    font-family: Lato;
    font-style: normal;
    font-size: 15px;
    padding-bottom: 22px;
    padding-top: 20px;
    outline: none;
}

.chat{
    position: relative;
    height: 70px;
    box-shadow: 0px 0.5px 0px rgba(0, 0, 0, 0.1);
    width: 100%;
}

.chatText{
    position: absolute;
    left:50%;
    top:50%;
    transform: translateX(-50%) translateY(-50%);
    font-family: 'Lato';
    font-style: normal;
    font-size: 22px;
    overflow: hidden;
}

.allMessages{
    position: relative;
    width: 100%;
    left:0px;
    top:0px;
    height: calc(100% - 131px);
    overflow-y: scroll;
}

.messageContainer{
    position: relative;
    width: 100%;
    left: 0px;
    overflow-y: scroll;
}

.messageBoxReceive{
    position: relative;
    width: 50%;
    background-color: #cccccc;
    border-radius: 10px;
    left: 5%;
    padding: 1px 15px;
    margin-bottom: 15px;
}

.messageBoxSend{
    position: relative;
    width: 50%;
    background-color: #cccccc;
    border-radius: 10px;
    left: 35%;
    padding: 1px 15px;
    margin-bottom: 15px;
}

.messageContainer > div > p{
    font-family: 'Lato';
    font-style: normal;
    font-size: 15px;
}

.closeIcon{
    position: absolute;
    right: 15px;
    top: 28px;
}

.closeIcon:hover{
    position: absolute;
    right: 15px;
    top: 28px;
    cursor: pointer;
}

#initiateCall:hover{
    opacity: 0.8;
    cursor: pointer;
}

.leftMenu{
    position: absolute;
    width: 306px;
    left: 15px;
    top: 15px;
}

.rightMenu{
    position: absolute;
    width: 306px;
    right: 0px;
    top: 15px;
}

.whiteMenu{
    position: relative;
    height: 86px;
    background: #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 4.4301px 4.4301px rgba(0, 0, 0, 0.25);
    border-radius: 11px;
    margin-bottom: 10px;
    padding: 0px;
}

.whiteMenuOpen{
    position: relative;
    height: 500px;
    left: 0px;
    top: 0px;
    background: #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 4.4301px 4.4301px rgba(0, 0, 0, 0.25);
    border-radius: 11.0753px;
}

.whiteMenuOpen:hover{
    position: relative;
    height: 500px;
    left: 0px;
    top: 0px;
    background: #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 4.4301px 4.4301px rgba(0, 0, 0, 0.25);
    border-radius: 11.0753px;
    cursor: pointer;
    opacity: 0.9;
}

.orangeMenu{
    position: relative;
    height: 86px;
    background: #FFB259;
    box-sizing: border-box;
    box-shadow: 0px 4.4301px 4.4301px rgba(0, 0, 0, 0.25);
    border-radius: 11px;
    margin-bottom: 10px;
}

.orangeMenuSmall{
    position: relative;
    height: 40px;
    background: #FFB259;
    box-sizing: border-box;
    box-shadow: 0px 4.4301px 4.4301px rgba(0, 0, 0, 0.25);
    border-radius: 11.0753px;
    margin-bottom: 10px;
}

.rightMenu > .whiteMenu{
    border-radius: 11px 0px 0px 11px;
}

.rightMenu > .orangeMenu{
    border-radius: 11px 0px 0px 11px;
}

.centerHeading{
    position: relative;
    left: 50%;
    top: 50%;
    width: 70%;
    transform: translateX(-50%) translateY(-50%);
}

.leftMenu > .whiteMenu > .centerHeading{
    position: relative;
    top: 50%;
    width: 70%;
}

.whiteMenuOpen > .centerHeading{
    position: relative;
    left: 50%;
    top: 33px;
    width: 70%;
    transform: translateX(-50%);
}

.menuHeading{
    text-align: center;
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    letter-spacing: 0.04em;
    margin: auto;
}

.whiteMenu > .centerHeading > .menuHeading{
    font-size: 16px;
    text-transform: uppercase;
    color: #486581;
}

.whiteMenuOpen > .centerHeading > .menuHeading{
    top: 20%;
    font-size: 16px;
    text-transform: uppercase;
    color: #486581;
}

.orangeMenu > .centerHeading > .menuHeading{
    font-size: 15px;
    color: #1E1C61;
}

.orangeMenuSmall > .centerHeading > .menuHeading{
    font-size: 15px;
    color: #1E1C61;
}

.menuOptional{
    text-align: center;
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 11px;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin: auto;
    padding-top: 5px;
}

.downLeftIcon{
    position: absolute;
    right: 6.25px;
    top: 28px;
}

.downRightIcon{
    position: absolute;
    left: 6.25px;
    top: 28px;
    transform: rotateZ(90deg);
}

.menuBack{
    position: relative;
    left: 0px;
    top: 0px;

    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.02em;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;

    color: #FFFFFF;
}

.orangeMenu:hover{
    cursor: pointer;
    opacity: 0.8;
}

.orangeMenuSmall:hover{
    cursor: pointer;
    opacity: 0.8;
}

.whiteMenu:hover{
    cursor: pointer;    
    opacity: 0.8;
}

.openDetailsContainer{
    position: relative;
}

.line{
    position: relative;
